@charset('UTF-8');

/* ----------- iPad mini ----------- */

/*
================================================
Portrait priority but supports some of Landscape
================================================
*/
@media only screen
and (min-device-width: 768px)
and (max-device-width: 1024px)
/*and (-webkit-min-device-pixel-ratio: 2) {*/
{
    /*
    ==========================
     base styles
    ===========================
     */

    /*.vh-align {*/
        /*position: absolute;*/
        /*top: 80%;*/
        /*left: 50%;*/
        /*-webkit-transform: translate(-50%,-80%);*/
        /*-moz-transform: translate(-50%,-80%);*/
        /*-ms-transform: translate(-50%,-80%);*/
        /*-o-transform: translate(-50%,-80%);*/
        /*transform: translate(-50%,-80%);*/
        /*transition: all 0.3s linear 0s;*/
    /*}*/

    .social-icons ul li {
        padding: 4px 10px;
    }

    .navbar {
        margin-top: 28px;
        min-height: 30px;
    }

      /*
    ==========================
     navbar
    ===========================
     */

     #wrapperhome {
        background-attachment: scroll;
    }

    /*
    ==========================
     navbar
    ===========================
     */

    .navbar-fixed-top .navbar-brand {
        font-size: 150%;
    }

    .navbar-fixed-top {
        padding: 0;
    }

    .navbar-nav {
        text-align: left;
        /*font-size: 75%;*/
        font-size: 100%;
    }

    .dropdown-menu {
        position: relative;
    }






    /*
    ==========================
     enquire
    ===========================
     */

    #enquire > .container > .row > .col-smxs-12 {
        height: 100%;
    }

    #enquire > .container > .row > .col-sm-12 {
        padding-top: 0;
        padding-bottom: 20px;
    }


    /*
    ==========================
     enquire - success
    ===========================
     */



    /*
   ==========================
    footer
   ===========================
    */

    #footer {
        /*height: 100%;*/
        text-align: center;
    }

    #footer .list-inline {
        margin-left: 0;
    }

    #footer > .navbar > .small {
        font-size: 85%;
    }

    #footer .ul-block {
        text-align: left;
        margin-left: -50px;
    }


        /*
    =======================
    scroll top button class
    =======================
    */

    .scroll-top {
        height: 30px;
    }

    .scroll-top .btn {
        padding: 3px 2px;
    }

}

/*
================================================
Landscape only
================================================
*/
@media only screen
and (min-device-width: 768px)
and (max-device-width: 1024px)
and (orientation: landscape) {

    /*
    ==========================
     base styles
    ===========================
     */
    /*
    ==========================
     navbar
    ===========================
     */

    .jumbotron .h1, .jumbotron h1 {
        font-size: 50px;
    }


    /*
    ==========================
     enquire - success
    ===========================
     */

     .dropdown-menu {
        position: absolute;
    }




}

@media only screen and (max-width: 736px)
{
    .navbar-default .navbar-brand {
        margin-top: -30px;
    }
}

