@charset('UTF-8');

/*----------- iPhone 6+ -----------*/

/*
================================================
Portrait priority but supports some of Landscape
================================================
*/
@media only screen
and (min-width: 414px)
and (max-width: 736px)
{
    /*
    ==========================
     base styles
    ===========================
     */

    /*.vh-align {*/
        /*position: absolute;*/
        /*top: 80%;*/
        /*left: 50%;*/
        /*-webkit-transform: translate(-50%,-80%);*/
        /*-moz-transform: translate(-50%,-80%);*/
        /*-ms-transform: translate(-50%,-80%);*/
        /*-o-transform: translate(-50%,-80%);*/
        /*transform: translate(-50%,-80%);*/
        /*transition: all 0.3s linear 0s;*/
    /*}*/

    .v-align {
        position: absolute;
        top: 80%;
        left: auto;
        -webkit-transform: translateY(80%);
        -moz-transform: translateY(80%);
        -ms-transform: translateY(80%);
        -o-transform: translateY(80%);
        transform: translateY(80%);
        transition: all 0.3s linear 0s;
    }

    h1, h2, h3, h4, h5, h6 {
        font-size: 130%;
        margin-top: 5px;
    }

    p {
     font-size: 85%;
    }

    .margin-top-30 { margin-top: 15px;}
    .margin-top-50 { margin-top: 25px;}
    .margin-top-70 { margin-top: 35px;}
    .margin-top-100 { margin-top: 50px;}

    .margin-bottom-50 { margin-bottom: 25px;}
    .margin-bottom-90 { margin-bottom: 45px;}
    .margin-bottom-100 { margin-bottom: 50px;}

    /*
    ==========================
     navbar
    ===========================
     */
    .social-icons ul li {
        padding: 4px 10px;
    }

    .navbar {
        margin-top: 28px;
        min-height: 30px;
    }

    .navbar-default .navbar-brand {
        /*-webkit-transform: scale(0.5);*/
        /*-moz-transform: scale(0.5);*/
        /*-ms-transform: scale(0.5);*/
        /*-o-transform: scale(0.5);*/
        /*transform: scale(0.5);*/
        /*margin-top: -30px;*/
        height: 30px;
        padding: 2px;
    }

    .navbar-toggle {
        padding: 2px auto;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    /*
    =====================================
    jumbotron messages - i love jlscustom
    =====================================
    */

    .jumbotron p {
        font-size: 14px;
    }

    .jumbotron a.btn {
        margin-bottom: 7px;
        padding: 6px 12px;
        font-size: 12px;
    }

     #wrapperhome {
        background-attachment: scroll;
    }

    /*#bghome .jumbotron h2 {*/
        /*font-size: 150%;*/
    /*}*/

    /*
    ==========================
     tables
    ===========================
     */

    .table tr th, .table tr td {
        font-size: 85%;
    }

        /*
    ==========================
     navbar
    ===========================
     */

    .navbar-fixed-top .navbar-brand {
        font-size: 175%;
    }

    .navbar-fixed-top {
        padding: 0;
    }

    #navhome .navbar-nav {
        margin: 0 -15px;
        text-align: left;
        font-size: 100%;
    }



    /*
    =======================
    scroll top button class
    =======================
    */

    .scroll-top {
        height: 30px;
    }

    .scroll-top .btn {
        padding: 3px 2px;
    }
}

