#factsaboutus .panel-heading {
    padding: 15px;
}

#factsaboutus .panel-heading.active {
	/*background: #75ADC8;*/
	/*background: #A48;*/
	background: #4474AA;
	color: #dddddd;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
	/*border-radius: 8px;*/
}

#factsaboutus .panel-group .panel-heading + .panel-collapse > .list-group, #factsaboutus .panel-group .panel-heading + .panel-collapse > .panel-body {
    border-top: none;
}

#faqs .panel-default > .panel-heading.active {
    color: #ffffff;
    /*background-color: #91C845;*/
    background-color: #91C845;
    border-color: #ECF0F1;
}

#faqs .panel-default > .panel-heading.active a:active,
#faqs .panel-default > .panel-heading.active a:focus,
#faqs .panel-default > .panel-heading.active a:hover {
    color: #ffffff;
    text-decoration: underline;
    outline: none;
    -webkit-transition: all 0.1s ease 0s;
       -moz-transition: all 0.1s ease 0s;
         -o-transition: all 0.1s ease 0s;
        -ms-transition: all 0.1s ease 0s;
            transition: all 0.1s ease 0s;
}

#faqs .panel-default > .panel-heading.active .text-green {
    color: #ffffff;
}

#usetgfbroker .panel-default > .panel-heading.active {
    color: #ffffff;
    background-color: #91C845;
    border-color: #ECF0F1;
}